.Footer {
  clear: both;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(0, 0, 0, 0.9)), to(transparent));
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.9) 50%, transparent);
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.9) 50%, transparent);
  background-image: linear-gradient(rgba(0, 0, 0, 0.9) 50%, transparent);
  text-align: center;
  overflow-y: hidden;
  color: #373737;
}

.Animation {
  opacity: 0;
  -webkit-transform: translateY(30px);
      -ms-transform: translateY(30px);
          transform: translateY(30px)
}

.Logo {
  float: left;
  margin:10px 10px;
  opacity: 0.3;
}

.Logos {
  overflow:auto;
  display: inline-block;
}


