
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: inherit;
            box-sizing: inherit; /*activate inheritance for the whole website and set the value in the body */
}

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; /*borders and paddings are not added to the total size of the box*/
  font-family: sans-serif;
}






