.Container {
  margin: auto;
  overflow: auto;
  font-family: 'Jost', sans-serif;
  height:100vh;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.Background {
  width: 100%;
  margin:0;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
  background-position: center;
}

.Shadow {
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(5%, rgba(0,0,0,.75)),color-stop(50%, rgba(0,0,0,.1)),color-stop(95%, #000));
  background-image: -webkit-linear-gradient(rgba(0,0,0,.75) 5%,rgba(0,0,0,.1) 50%,#000 95%);
  background-image: -o-linear-gradient(rgba(0,0,0,.75) 5%,rgba(0,0,0,.1) 50%,#000 95%);
  background-image: linear-gradient(rgba(0,0,0,.75) 5%,rgba(0,0,0,.1) 50%,#000 95%);
}
.Courtain {
  background-color: rgba(0,0,0,0.5);
  width:100%;
}

@media (min-width: 300px){
  .Container {
    max-width: 100%;
  }
}
@media (min-width: 600px){
  .Container {
    max-width: 540px;
  }
}
@media (min-width: 800px){
  .Container {
    max-width: 740px;
  }
}
@media (min-width: 1200px){
  .Container {
     max-width: 1140px;
  }
 }


